<template>
  <v-app id="inspire" class="overflow-hidden" style="background: white !important; background-color: white !important;"> 
    <v-navigation-drawer v-model="drawer" dark app style="z-index: 204;">
      <v-list dense>
        <v-list-item>
          <v-list-item-content>
            <v-list-item-title>
              <h1>Layers</h1>
              <v-list dense class="ml-3" >
                <v-switch v-model="sLeak" label="Leaks" @change="toggled" class="py-0 mb-0"></v-switch>
                <v-switch v-model="sStation" label="Stations" @change="toggled" class="py-0 my-0"></v-switch>
                <v-switch v-model="sRoute" label="Routes" @change="toggled" class="py-0 my-0"></v-switch>
                <v-switch v-model="sRegion" label="Regions" @change="toggled" class="py-0 my-0"></v-switch>
              </v-list>
            </v-list-item-title>
          </v-list-item-content>
        </v-list-item>
        <v-divider></v-divider>
        <v-expansion-panels multiple accordion v-model="expand"  class="ml-0 pl-0">
          <v-expansion-panel>
            <v-expansion-panel-header>Regions</v-expansion-panel-header>
            <v-divider></v-divider>
            <!-- Filter by region -->
            <v-expansion-panel-content>
              <v-list-item-content >
                <v-switch v-model="regions.cent" label="Central" class="py-0 my-0 mr-5"></v-switch>
                <v-switch v-model="regions.east" label="Eastern" class="py-0 my-0"></v-switch>
                <v-switch v-model="regions.rock" label="Rocky Mountain" class="py-0 my-0"></v-switch>
                <v-switch v-model="regions.rose" label="Wildrose" class="py-0 my-0"></v-switch>
                <!-- <v-autocomplete :items="communities" item-text="properties.name" label="Communities" outlined multiple
                   dense v-model="filterLeft.community"></v-autocomplete> </-->
              </v-list-item-content>
            </v-expansion-panel-content>
          </v-expansion-panel>
        </v-expansion-panels>
        <!-- Emissions -->
        <v-expansion-panels multiple accordion v-model="expand2"  class="ml-0 pl-0">
          <v-expansion-panel>
            <v-expansion-panel-header>Emissions</v-expansion-panel-header>
            <v-divider></v-divider>
            <v-expansion-panel-content>
              <v-list-item-content>
                <v-switch v-model="emissions.le" label="Leaks" @change="toggled2" class="py-0 my-0 mr-5"></v-switch>
                <v-switch v-model="emissions.re" label="Repaired" @change="toggled2" class="py-0 my-0 mr-5" :disabled="!emissions.le"></v-switch>
                <v-switch v-model="emissions.dr" label="Driving" @change="toggled" class="py-0 my-0"></v-switch>
              </v-list-item-content>
            </v-expansion-panel-content>
          </v-expansion-panel>
        </v-expansion-panels>
      </v-list>
      <v-select :items="rounds" item-disabled="disabled" label="Rounds" v-model="sRounds" required class='px-2 pt-4' filled multiple dense> </v-select>
      <v-divider></v-divider>
      <v-col align="center">
        <v-btn color="red">reset</v-btn>
      </v-col>

    </v-navigation-drawer>
    <!-- Top Nav Bar -->
    <v-app-bar app color="Black" dark class="pl-2">
      <v-app-bar-nav-icon @click.stop="drawer = !drawer" />
      <v-toolbar-title class="font-weight-black">NEBA</v-toolbar-title>
      <v-spacer />
      <!--
      <v-tooltip bottom>
        <template v-slot:activator="{ on }">
          <v-btn icon dark v-on="on" @click="about = !about">
            <v-icon large> mdi-information</v-icon>
          </v-btn>
        </template>
        <span>Learn more about the app!</span>
      </v-tooltip> -->
      <!--
      <v-dialog v-model="about" width="700" scrollable>
        <v-card height="500">
          <v-card-title class="headline grey lighten-2" primary-title>About CCC!</v-card-title>

          <v-card-text class="pt-2">
          </v-card-text>

          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn @click="about = false" icon>
              <v-icon>mdi-close</v-icon>
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog> </-->
    </v-app-bar> 
    <v-card flat style="position: absolute; bottom: 0; left: 0; z-index: 2;" width="600" height="350" dark id="myChart" >
       <canvas id="emissionChart"></canvas>
    </v-card>
    <!-- Leaflet Map --> 
    <v-main>
      <v-container class="fill-height pa-0 ma-0" fluid>
        <mymap />
      </v-container>
    </v-main>
  </v-app>
</template>

<script>
  import mymap from "./leaflet";
  import Chart from 'chart.js';
  import {eventBus} from '../main';
  //import axios from "axios";
  var chart;
  var ctx;

  export default {
    name: "toolbar",
    components: {
      mymap
    },
    props: {
      source: String
    },
    data: () => ({
      sheet: false,
      about: false,
      dialog: false,
      dialogTitle: "",
      sLeak: false,
      sRegion: true,
      sRoute: false,
      sStation: false,
      sRounds: ["1"],
      comment: '',
      menu: false,
      drawer: false,
      regions: {
        rose: true,
        cent: true,
        rock: true,
        east: true
      },
      rounds: [
        {text: "1"}, 
        {text: "2", disabled: true},
        {text: "3", disabled: true}],
      hours: null,
      killo: null,
      expand: [],
      expand2: [],
      emissions: {
        le: true,
        re: false,
        dr: false
      },
      chartData: null,
    }),

    mounted() {
      eventBus.$on("make-chart", data => {
        console.log(data);
        ctx = document.getElementById('emissionChart');
        chart = new Chart(ctx, {
          type: 'horizontalBar',
          data: data,
          options: {
            legend: {
              labels: {
                fontColor: "white"
              }
            }
          }
        });
        this.chart = chart;
      });

      
      eventBus.$on("pass-chart", data => {
        this.chartData = data;
      });
    },

    methods: {
      toggled() {
        //if leaks toggled off
        if (!this.sLeak) {
          eventBus.$emit("toggle-leak-off");
        } else if (this.sLeak) {
          eventBus.$emit("toggle-leak-on")
        }

        //if regions toggled off
        if (!this.sRegion) {
          eventBus.$emit("toggle-region-off");
        } else if (this.sRegion) {
          eventBus.$emit("toggle-region-on")
        }

        //if routes toggled off
        if (!this.sRoute) {
          eventBus.$emit("toggle-route-off");
        } else if (this.sRoute) {
          eventBus.$emit("toggle-route-on")
        }

        //if station toggled off
        if (!this.sStation) {
          eventBus.$emit("toggle-station-off");
        } else if (this.sStation) {
          eventBus.$emit("toggle-station-on")
        }

        if (!this.emissions.dr) {
          eventBus.$emit("toggle-drive-off");
        } else if (this.emissions.dr) {
          eventBus.$emit("toggle-drive-on")
        }
      },
      toggled2() {
        if (this.emissions.le && this.emissions.re) {
          eventBus.$emit("toggle-heat-off");
          eventBus.$emit("toggle-repair-on");
        } else if (this.emissions.le && !this.emissions.re) {
          eventBus.$emit("toggle-heat-on");
          eventBus.$emit("toggle-repair-off");
        } else if (!this.emissions.le) {
          this.emissions.re = false;
          eventBus.$emit("toggle-heat-off");
          eventBus.$emit("toggle-repair-off");
        }
      },
    },
  }

</script>


<style scoped>
 
  #myChart {
    opacity: 0.6;
  }

  #myChart:hover {
    opacity: 1;
  }

</style>